.newsAdd__errorValidation input{
  outline: 1px solid red;
}

.newsAdd__errorValidation textarea{
  outline: 1px solid red;
}

.newsAdd__errorValidation label{
  display: block;
  margin-top: -8px;
  margin-bottom: 5px;
  color: red;
}


.formAddNews{
  position: relative;
  border: 3px solid green;
  padding: 15px;
}

  .formAddNews__title{
    font-size: 110%;
    background-color: black;
    position: absolute;
    top: -12px;
    left: 10px;
    padding: 0 10px;
  }

  .formAddNews__inpAutor{
    display: block;
    width: 280px;
    margin-bottom: 10px;
  }

  .formAddNews__inpTitle{
    display: block;
    width: 280px;
    margin-bottom: 10px;
  }

  .formAddNews__textNews{
    display: block;
    width: 280px;
    margin-bottom: 10px;
  }

  .formAddNews__button{
    margin-top: 10px;
  }





