.newsList__checkbox{
  display: block;
  float: left;
  height: auto;
  width: auto;
}

.newsList__title{
  font-size: 20px;
  font-weight: bold;
}

.newsList__li{
  margin-bottom: 20px;
}

.newsList__author{
  font-weight: bold;
}


/* animation - ReactCSSTransitionGroup */
.newsAnimation-enter {
  opacity: 0.01;
}

.newsAnimation-enter.newsAnimation-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.newsAnimation-leave {
  opacity: 1;
}

.newsAnimation-leave.newsAnimation-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}
/*END animation - ReactCSSTransitionGroup */



/* pagination */
.pagination {
  font-size: 20px;
}
  .pagination li {
    display: inline-block;
    margin: 5px;
  }  
  .pagination li a {
    color: white;
  }  
  .pagination li.active a {
    color:red;
  }
/*END pagination */



